const generateStars = (count) => {
  return Array.from({ length: count }, (_, i) => ({
    id: i,
    left: `${Math.random() * 100}%`,
    top: `${Math.random() * 100}%`,
    opacity: Math.random() * 0.7 + 0.3,
    blur: Math.random() * 2 + 1,
  }));
};

const stars = generateStars(20);

const StarSky = () => {
  const [selectedStar, setSelectedStar] = useState(null);
  const [randomImage, setRandomImage] = useState(null);

  useEffect(() => {
    if (selectedStar !== null) {
      const content = starContents[selectedStar % starContents.length];
      setRandomImage(
        content.images[Math.floor(Math.random() * content.images.length)]
      );
    }
  }, [selectedStar]);

  return (
    <div className="star-sky">
      {stars.map((star) => (
        <img
          key={star.id}
          src={starImage}
          alt={`Star ${star.id}`}
          className="star"
          style={{
            position: "absolute",
            left: star.left,
            top: star.top,
            opacity: star.opacity,
            filter: `blur(${star.blur}px)`,
            cursor: "pointer",
          }}
          onClick={() => setSelectedStar(star.id)}
        />
      ))}

      {selectedStar !== null && (
        <div style={{ position: "relative", marginTop: "3%" }}>
          <div className="modal" onClick={() => setSelectedStar(null)}>
            <div className="modal-content">
              <h2>Письмо для тебя:</h2>
              <TypeAnimation
                sequence={[
                  starContents[selectedStar % starContents.length].text,
                  500,
                ]}
                wrapper="p"
                speed={50}
                cursor={false}
                style={{ whiteSpace: "pre-line", paddingBottom: "40px" }}
              />

              <HighlightOffIcon
                className="CloseModalBtn"
                onClick={() => setSelectedStar(null)}
              />
            </div>
            <div className="random-image">
              {randomImage && (
                <img
                  className="modal-image"
                  src={randomImage}
                  alt="Valentine"
                />
              )}
            </div>
          </div>
          <span class="heart-falling">&hearts;</span>
          <span class="heart-falling">&hearts;</span>
          <span class="heart-falling">&hearts;</span>
          <span class="heart-falling">&hearts;</span>
          <span class="heart-falling">&hearts;</span>
          <span class="heart-falling">&hearts;</span>
        </div>
      )}
    </div>
  );
};

export default StarSky;
